:root {
    @include after-mobile {
        --my-account-tab-list-background: #{$white};
    }
}

.MyAccountTabList {
    @include after-mobile {
        padding: 1.2rem 0;
        background-color: var(--my-account-tab-list-background);
    }

    &-ExpandableContentContent {
        @include after-mobile {
            margin-top: 0;
        }

        &_isContentExpanded {
            @include mobile {
                padding: 0 1.4rem 1.4rem;
            }
        }
    }

    &-ExpandableContentButton {
        @include after-mobile {
            display: none;
        }

        &::before,
        &::after {
            right: 2.4rem;

            @include mobile {
                right: 2.8rem;
            }
        }
    }
}
