.MyAccountDashboard {
    display: grid;
    grid-template-columns: 1fr;
    grid-gap: 2.4rem;

    @include mobile {
        grid-gap: 2.8rem;
    }

    @include desktop {
        grid-template-columns: 1fr 1fr;
        grid-gap: 4.8rem;
    }

    &-DefaultAddress {
        @include desktop {
            grid-row: 2;
        }
    }

    &-CustomerData {
        @include desktop {
            grid-column: span 2;
        }
    }

    &-Info {
        margin-bottom: 5px;
    }

    button + button {
        margin-top: 1.2rem;

        @include mobile {
            margin-top: 1.4rem;
        }

        @include after-mobile {
            margin-left: 1.2rem;
        }
    }
}
